export default {
	data() {
		return {
		};
	},
	props: {
		tabs: {
			type: Array,
			value: []
		}
	},
	methods:{
	handleItemTap(e){
		const{index} =e.currentTarget.dataset
		this.$emit('tabsItemChange',index ,e )
	}
	}
};
